.todoList {
	margin-bottom: 1em;
	& form {
		position: relative;
		width: 100%;
		.addTodo {
			background: inherit;
			border: none;
			outline: none;
			font-size: 1em;
			border-bottom: 1px black solid;
			width: 100%;
			padding-left: 1em;
		}
		.submitTodo {
			background-color: transparent;
			color: $theme-color;
			font-weight: bold;
			position: absolute;
			left: 0;
			font-size: 1em;
			padding: 0;
		}
	}
}